/* General Styles */
body {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f4f4;
    color: #000;
}

h1,
h2,
h3 {
    color: #000;
    margin-bottom: 10px;
}

.team-header {
    text-align: center;
    padding-bottom: 10px;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

/* Header */
header {
    background-color: #004f9f;
    color: white;
    padding: 20px;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.header-content {
    display: flex;
    align-items: center;
    justify-content: center; /* Center content horizontally */
}

.header-logo {
    width: 100px;
    height: auto;
    margin-right: 20px;
    /* float: left;
    padding-right: 20%; */
}

.header-text {
    text-align: left;
}

header h1 {
    margin: 0;
    font-size: 2.5em;
    color: white;
}

header a {
    text-decoration: none;
}

header p {
    margin: 5px 0 0;
    font-size: 1.2em;
    color: white;
}

/* Navbar */
nav {
    background-color: #000;
    overflow: hidden;
    margin-bottom: 20px;
}

nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
}

nav ul li {
    margin: 0 15px;
}

nav ul li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 20px;
    text-decoration: none;
    transition: background-color 0.3s;
}

nav ul li a:hover {
    background-color: #575757;
}

.home-text {
    margin-left: 20px;
    /* text-align: center; */
}

